<template>
  <v-photoswipe v-if="imageList && imageList.length && isOpen" :isOpen="isOpen" :items="imageList" :options="options"
                @close="hidePhotoSwipe"></v-photoswipe>
</template>
<script>
import {PhotoSwipe, PhotoSwipeGallery} from "v-photoswipe";
import * as _ from "underscore";

export default {
  props: {
    events: {},
    imageEvent: {}
  },
  data: () => {
    return {
      isOpen: false,
      isOpenGallery: false,
      type: '',
      clientWidth: 1000,

      options: {
        index: 0,
        arrowEl: false,
        fullscreenEl: false,
        shareEl: false
      },
      optionsGallery: {},
      imageList: []
    }
  },
  // components: {
  //   'v-photoswipe': PhotoSwipe,
  //   'v-photoswipe-gallery': PhotoSwipeGallery,
  // },
  computed: {},
  watch: {
    imageEvent: function (newVal, oldVal) { // watch it
      this.openGallery()
      console.log(newVal, "new WAl")
    }
  },
  methods: {
    showPhotoSwipe(index) {
      this.isOpen = true
      this.$set(this.options, 'index', index)
    },

    hidePhotoSwipe() {
      this.isOpen = false
    },

    openGallery() {
      this.imageList = []
      console.log(this.events, "events")
      _.each(this.events, event => {
        if (event.event.content.msgtype === 'm.image') {
          this.imageList.push({
            src: event.event.content.url,
            w: event.event.content.info.w,
            h: event.event.content.info.h,
            eventId: event.event.event_id
          })

        }

      })

      let imgIndex = this.imageList.map(function (e) {
        return e.eventId
      }).indexOf(this.imageEvent.event.event_id)

      this.showPhotoSwipe(imgIndex)
    },

  }
}
</script>