import {mapState} from 'vuex';
import chatPreview from '@/components/chat/preview/index.vue'
import {Swiper, SwiperSlide} from 'vue-awesome-swiper'
import _ from 'underscore'
import f from "@/application/functions";

export default {
  name: 'chatJoin',
  props: {
    chat: Object,
    usersinfo: Array
  },

  components: {
    chatPreview,
    Swiper,
    SwiperSlide
  },

  data: function () {

    return {
      loading: false,
      joinedMembers: []
    }

  },

  created: () => {

  },

  watch: {
    //$route: 'getdata'
  },
  computed: mapState({
    auth: state => state.auth,
    pocketnet: state => state.pocketnet,
    users: function () {

      let pStateUsers = this.$store.state.users
      let roomMembers = this.chat.currentState.members
      let pUsers = []
      let members = []

      _.mapObject(roomMembers, function (val, key) {
        pUsers.push({id: f.getmatrixid(key)})
      })
      _.map(pStateUsers, function (id) {
        members.push(id)
      })

      this.joinedMembers = f.filterByUserId(pUsers, members)

      var users = _.filter(_.map(this.chat.currentState.members || [], (m) => {
        return this.$f.deep(this, '$store.state.users.' + m.name) || null
      }), (m) => {
        return m && m.id !== this.core.user.userinfo?.id
      })

      return users

    },
  }),
  mounted : function(){
 
  },

  methods: {
    encrypt : function(algorithm){

      return this.core.mtrx.client.setRoomEncryption(this.chat.roomId, {
        algorithm : algorithm
      })
      
    },
    join: function () {
      this.core.mtrx.client.joinRoom(this.chat.roomId).then(() => {

        /*var need = this.needEncryption()

        if (need){

          var algorithm = "m.olm.v1.curve25519-aes-sha2"
          this.encrypt(algorithm).then(r => {
            this.$emit('joined')
          })

          return
        }*/
        

        this.$emit('joined')
      })
    },

    decline: function () {
      this.core.mtrx.client.leave(this.chat.roomId)
      this.$router.go(-1)
    },

    needEncryption : function(){
      

      var encrypted = this.core.mtrx.client.isRoomEncrypted(this.chat.roomId)

      if (encrypted) return false

      return _.toArray(this.chat.currentState.members).length == 2

      var room = this.core.mtrx.client.getRoom(this.chat.roomId)

      if(!room) return false

      var ev = this.core.mtrx.client._roomList.isRoomEncrypted(this.chat.roomId);

      if (ev) return false

      return this.core.mtrx.customRoomType(this.chat.roomId) == 'tetatet'

    }
  },
}