import {mapState} from 'vuex';
import chatName from '@/components/chats/assets/name.vue'
import chatIcon from '@/components/chats/assets/icon.vue'
import userView from '@/components/user/view/pnuser/index.vue'
import {Swiper, SwiperSlide} from 'vue-awesome-swiper'
import 'swiper/swiper-bundle.css'
//
import {Carousel, Slide} from 'vue-carousel';
//

export default {
  name: 'chatPreview',
  props: {
    chat: Object,
    usersinfo: Array,
    users: {},
    members: Array
  },

  components: {
    userView,
    chatName,
    chatIcon,
    Swiper,
    SwiperSlide,
    Carousel,
    Slide
  },

  data: function () {

    return {
      loading: false,
      usersinfoLocal: [],
      swiperOption: {
        direction: 'horizontal',
        pagination: {
          el: '.swiper-pagination',
          clickable: true
        }
      }
    }

  },

  computed: mapState({
    auth: state => state.auth
  }),

  mounted: function () {
    this.setUsersInfo()

  },

  methods: {
    setUsersInfo: function () {
      if (this.chat) {
        var members = _.filter(_.map(_.filter(this.chat?.currentState.getMembers() || {}, (m) => {
          return this.$f.getmatrixid(m.name) !== this.core.user.userinfo?.id

        }), m => {
          return this.$f.deep(this, '$store.state.users.' + this.$f.getmatrixid(m.name)) || null
        }), m => {
          return m
        })

        this.usersinfoLocal = members
      } else {
        this.usersinfoLocal = this.usersinfo
      }

    },

  },
}